* { box-sizing: border-box; }
ul { list-style-type: none; padding: 0; margin: 0; }

.Wrapper {
  width: 40rem;
  margin: 3rem auto;
  box-shadow: 4px 4px 20px hsla(0, 0%, 0%, 0.2);
  border-radius: 4px;
}

.Tabs {
  display: flex;
  height: 2.4rem;
  background-color: hsl(0, 0%, 83%);
}

.Tab {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 1rem;
  cursor: pointer;
}

.Tab:hover {
  background-color: hsl(0, 0%, 75%);
}

.ActiveTabContent {
  padding: 1rem;
  text-align: start;
  line-height: 1.5rem;
  color: hsl(0, 0%, 20%);
}

